@import url("https://fonts.googleapis.com/css2?family=Mina:wght@400;700&display=swap");
:root {
  --minecraft-font: "Minecraft";
  --mina-regular-font: Mina;
  --mina-weight-regular: 400;
  --mina-weight-bold: 700;

  --background-color: #282c34;
  --text-color: rgb(0, 0, 0);
  --text-active-color: #ffffff;
  --link-color: #61dafb;
  --button-color: #ff0000;
  --button-hover-color: #ffe2e2;
  --button-active-color: #b72020;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@font-face {
  font-family: "Minecraft";
  src: url("./fonts/Minecraft.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "MinaR";
  src: url("./fonts/Mina-Regular.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "MinaB";
  src: url("./fonts/Mina-Bold.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}
body {
  font-family: "Minecraft";
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  background-color: #b32024; /* Red background */
  /* overflow: hidden; */
}
hr {
  border: 0;
  background: #333;
}
.announcement-container {
  background-color: #f8f8f8;
  text-align: center;
}
.announcement-logo {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}

.announcement-logo .text-wrapper {
  position: absolute;
  background-color: rgb(255, 255, 255); /* Semi-transparent background */
}
.announcement-logo .text {
  color: #333;
}
.message {
  font-family: MinaR;
  font-weight: 900;
  color: #333;
}
.sub-message {
  font-family: MinaB;
  font-weight: bold;
  color: #333;
}
.email-input {
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}
.email-input input[type="email"] {
  border: none;
  outline: none;
  font-family: MinaB;
}
.button-search {
  background-color: #ff0000;
  color: white;
  cursor: pointer;
  font-family: Minecraft;
  display: flex;
  justify-content: center;
  align-items: center;
}

.button-search:hover,
.button-back:hover {
  background-color: #ffffff;
  color: #000000;
}

@media (max-width: 500px) {
  hr {
    border: 0;
    height: 1px;
    margin-top: 15px;
    margin-bottom: 10px;
  }

  .announcement-container {
    max-width: 250px;
    min-width: 100px;
    height: auto;
    width: 100%;

    padding-inline: 40px;
    padding-top: 20px;
    padding-bottom: 40px;
    border-radius: 15px;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2);
    border: 5px solid #333;
  }

  .announcement-logo img {
    width: 120px;
    height: auto;
  }
  .announcement-logo .text-wrapper {
    top: 25px;
    font-size: 21px;
  }
  .announcement-logo .text {
    font-size: 20px;
  }
  .message {
    font-size: 12px;
    margin: 20px;
  }
  .sub-message {
    font-size: 15px;
    margin-bottom: 12px;
  }
  .email-input {
    border: 2px solid #333;
    border-radius: 5px;
  }
  .email-input input[type="email"] {
    width: 100%;
    font-size: 14px;
  }
  .button-search {
    border: 2px solid #000000;
    width: 60px; /* Adjusted width */
    height: 40px; /* Adjusted height */
    font-size: 26px; /* Adjusted font size */
    padding-top: 7px;

    transition: transform 0.05s ease, box-shadow 0.05s ease;
    box-shadow: 2px 1px 0px 1px rgb(0, 0, 0);
  }
  .button-search:hover,
  .button-back:hover {
    transform: translate(-3px, -3px);
    box-shadow: 5px 4px 0px 1px rgb(0, 0, 0);
  }

  .button-search:active,
  .button-back:active {
    transform: translate(0px, 0px);
    box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.5);
    box-shadow: 2px 1px 0px 1px rgb(0, 0, 0);
  }
}

/* Media queries for desktop */
@media (min-width: 501px) {
  hr {
    border: 0;
    height: 1px;
    margin-top: 15px;
    margin-bottom: 10px;
  }

  .announcement-container {
    max-width: 500px;
    min-width: 400px;
    height: auto;
    width: 100%;

    padding-inline: 40px;
    padding-top: 20px;
    padding-bottom: 40px;
    border-radius: 15px;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2);
    border: 5px solid #333;
  }

  .announcement-logo img {
    width: 140px;
    height: 60px;
  }
  .announcement-logo .text-wrapper {
    top: 25px;
    font-size: 21px;
  }
  .announcement-logo .text {
    font-size: 20px;
  }
  .message {
    font-size: 17px;
    margin: 20px;
  }
  .sub-message {
    font-size: 18px;
    margin-bottom: 12px;
  }
  .email-input {
    border: 2px solid #333;
    border-radius: 5px;
  }
  .email-input input[type="email"] {
    width: 100%;
    font-size: 14px;
  }
  .button-search {
    border: 2px solid #000000;
    width: 70px; /* Adjusted width */
    height: 50px; /* Adjusted height */
    font-size: 26px; /* Adjusted font size */
    padding-top: 7px;

    transition: transform 0.05s ease, box-shadow 0.05s ease;
    box-shadow: 2px 1px 0px 1px rgb(0, 0, 0);
  }
  .button-search:hover,
  .button-back:hover {
    transform: translate(-3px, -3px);
    box-shadow: 5px 4px 0px 1px rgb(0, 0, 0);
  }

  .button-search:active,
  .button-back:active {
    transform: translate(0px, 0px);
    box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.5);
    box-shadow: 2px 1px 0px 1px rgb(0, 0, 0);
  }
}
